.reg {
  .header {
    height: initial;
    #myVideo {
      position: fixed;
    }
    .registration {
      height: 100%;
      position: relative;

      padding: 0;
      margin: 0;
      display: flex;
      justify-content: center !important;
      align-items: center !important;
      form {
        position: fixed;
        // position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        width: initial;
        z-index: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 100%;
        @media screen and (max-width: 425px) {
          width: 95%;
        }
        @media screen and (max-height: 600px) {
          //   width: 95%;
          transform: initial;
          position: initial;
          top: initial;
          left: initial;
        }
        h1 {
          margin: 30px 0;
          text-transform: uppercase;
          font-family: "Roboto", sans-serif;
          font-size: 42px;
          font-weight: 500;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: 2.1px;
          text-align: center;
          color: #ffffff;
          transition: transform 2s;
          @media screen and (max-width: 425px) {
            font-size: 10vw;
          }
        }
        .form__question {
          margin: 0 0 15px;
          h3,
          p {
            text-transform: uppercase;
            font-family: "Roboto", sans-serif;
            font-size: 10px;
            font-weight: normal;
            font-stretch: normal;
            font-style: normal;
            line-height: normal;
            letter-spacing: 1px;
            text-align: center;
            color: #656565;
            @media screen and (max-width: 768px) {
              font-size: 14px;
            }
          }
          p {
            color: #ffffff;
            a {
              color: inherit;
              text-decoration: none;
              cursor: pointer;
            }
          }
        }
        .form__input[type="submit"]:focus {
          transform: scale(1.05);
          // background: red;
          // background: rgba(0, 0, 0, 0.2);
        }
        .form__label {
          //   text-transform: uppercase;
          font-family: "Roboto", sans-serif;
          font-size: 1.2rem;
          margin-left: 2rem;
          margin-top: 0.7rem;
          display: block;
          transition: all 0.3s;
          color: white;
          transform: translateY(-7rem);
        }
        .form__submit {
          text-transform: uppercase;
        }
        .big-text {
          text-transform: uppercase;
          text-align: center;
          text-decoration: none;
        }
        .form__input,
        .form__link {
          //   text-transform: uppercase;
          font-family: "Roboto", sans-serif;
          color: #333;
          font-size: 1.2rem;
          margin: 0 auto;
          padding: 1.5rem 2rem;
          border-radius: 0.2rem;
          background-color: rgb(255, 255, 255);
          border: none;
          // width: 90%;
          width: 100%;
          // opacity: 0.1;
          display: block;
          border-bottom: 0.3rem solid transparent;
          transition: all 0.3s;
        }
        .form__input:placeholder-shown {
          opacity: 0.45;
        }
        .form__input:placeholder-shown + .form__label {
          opacity: 0;
          visibility: hidden;
          -webkit-transform: translateY(-4rem);
          transform: translateY(-4rem);
        }
      }
    }
  }
  .header::before {
    position: fixed;
  }
}
